<template>
  <canvas></canvas>
</template>

<style scoped>
canvas {
  width: 100%;
}
</style>

<script>
import WordCloud from 'wordcloud'

export default {
  props: [
    'data',
    'options'
  ],
  mounted () {
    this.processCloud()
  },
  watch: {
    data () {
      this.processCloud()
    }
  },
  methods: {
    processCloud () {
      const list = this.data
      const hover = (item) => this.$emit('item-hover', item)
      const options = Object.assign({
        color: (word, weight) => `hsl(${
          (125 + weight) * 5}, 95%, ${
          100 - (100 - weight) / 4}%)`,
        backgroundColor: 'transparent',
        ellipticity: 0.382,
        rotateRatio: 0,
        scale: 100,
        wait: 100,
        hover,
        list
      }, this.options)

      const maxWeight = list.reduce((max, el) => {
        return Math.max(max, el[1])
      }, -Infinity)

      if (maxWeight !== 100) {
        list.forEach(e => {
          e[2] = e[1]
          e[1] *= options.scale / maxWeight
        })
      }

      const canvas = this.$el
      canvas.width = canvas.offsetWidth
      canvas.height = canvas.width * 0.382

      WordCloud(canvas, options)
    }
  }
}
</script>



